<template>
  <div class="step-wrap" id="step6"  v-loading="fullscreenLoading">
    <div class="step-header-wrap">
      <h2 style="background-color: white;">生成文书</h2>
    </div>
    <div class="step-body-without-tabs-wrap step-body-without-tabs-wrap2"  style="padding:0;">
      <!-- <iframe :src="previewSrc" class="step6-iframe" style="border:none;display:block;width:100%;height:100%;"></iframe> -->
      <div id="generatedWs" v-html="wsHtml"></div>
    </div>
    <div class="step-footer-wrap">
      <el-dropdown class="el-dropdown-step6" placement="100, 100"  @command="handleClick">
        <el-button size="small">
          导出<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu class="el-dropdown-menu-step6"  slot="dropdown">
          <el-dropdown-item command="exportsWORD">Word格式</el-dropdown-item>
          <el-dropdown-item command="exportsPPT">PPT格式</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button size="small" @click="prev">上一步</el-button>
      <el-button size="small" type="primary" @click="done">完成</el-button>
    </div>
  </div>
</template>

<script>
import { isExistWsTemplate } from '@/api/root'
import { getAjaxPromise } from '@/api/admin'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'step6',
  data() {
    return {
      msg: 'step6',
      previewSrc: '',
      wsHtml: '',
      fullscreenLoading: false
    }
  },
  mounted() {
    // this.loadData()
    this.previewSrc = `/api/4static/generated/ws/preview?caseNumber=${
      this.caseObj.caseNumber
    }`
    this.fullscreenLoading = true
    const saveOrUpdateCaseBaseInfoPromise = getAjaxPromise({
      method: 'get',
      url: this.previewSrc,
      headers: {
        contentType: 'text/html;charset=UTF-8'
      },
      data: this.contentForm
    })
    saveOrUpdateCaseBaseInfoPromise
      .then(res => {
        this.fullscreenLoading = false
        this.wsHtml = res.data
      })
      .catch(respon => {
        this.fullscreenLoading = false
      })
  },
  methods: {
    // loadData() {
    //   previews({
    //     caseNumber: this.caseObj.caseNumber
    //   }).then(res => {
    //     this.loadHtml = res.data
    //   })
    // },
    handleClick(command) {
      if (command === 'exportsWORD') {
        this.exportsWord()
      } else if (command === 'exportsPPT') {
        this.exportsPPT()
      }
    },
    exportsWord() {
      this.fullscreenLoading = true
      isExistWsTemplate({})
        .then(res => {
          if (res.data.code === '200') {
            this.fullscreenLoading = false
            if (res.data.data.wordExist) {
              return res.data.data.wordExist
            } else {
              this.$message({
                type: 'warning',
                message: '没有配置该模板'
              })
            }
          } else {
            this.fullscreenLoading = false
            this.$message({
              type: 'warning',
              message: res.data.msg
            })
          }
        })
        .then(v => {
          if (v) {
            this.fullscreenLoading = false
            window.location = `/api/4static/generated/ws/export?caseNumber=${
              this.caseObj.caseNumber
            }&templateType=doc`
          }
        })
        .catch(respon => {
          this.fullscreenLoading = false
        })
    },
    exportsPPT() {
      this.fullscreenLoading = true
      isExistWsTemplate({})
        .then(res => {
          if (res.data.code === '200') {
            this.fullscreenLoading = false
            if (res.data.data.pptExist) {
              return res.data.data.pptExist
            } else {
              this.$message({
                type: 'warning',
                message: '没有配置该模板'
              })
            }
          } else {
            this.fullscreenLoading = false
            this.$message({
              type: 'warning',
              message: res.data.msg
            })
          }
        })
        .then(v => {
          if (v) {
            this.fullscreenLoading = false
            window.location = `/api/4static/generated/ws/export?caseNumber=${
              this.caseObj.caseNumber
            }&templateType=ppt`
          }
        })
        .catch(respon => {
          this.fullscreenLoading = false
        })
    },
    done() {
      this.$confirm('是否退出案件?', '', {
        confirmButtonText: '退出',
        cancelButtonText: '取消',
        center: true,
        showClose: false
      }).then(() => {
        if (eDossier) {
          eDossier.close()
        }
      })
    },
    prev() {
      this.$router.push({ name: 'step5' })
    }
  }
}
</script>
<style lang='scss'>
#generatedWs {
  position: absolute;
  top: 33px;
  left: 0;
  width: 100%;
  height: calc(100% - 76px);
  overflow-y: scroll;
  overflow-x: hidden;
  zoom: 1.5;
}

#generatedWs > div {
  width: 94% !important;
  margin: 0 40px !important;
}

#generatedWs table {
  width: 100% !important;
}

#generatedWs {
  span,
  a,
  div,
  p,
  li {
    font-size: 12px !important;
  }
}
.el-dropdown-step6 {
  width: 130px;
  margin-right: 10px;
}
.el-dropdown-menu-step6 {
  text-align: center;
  top: -91px;
  left: 13px;
}
#step6 {
  .step-header-wrap {
    h2 {
      font-size: 14px;
      color: #333;
      font-weight: 600;
      text-indent: 24px;
    }
  }
}
</style>



